
  /* ---------- Button styles ------------------ */
  
  .navbar {
    padding-top: 50px;
    text-align: center;
   
  }
  
  .navbar h1 {
    padding-bottom: 20px;
    text-transform: capitalize;
  }
  
  /** button group styles **/
  .btn_group {
    border-radius: 1rem;
    text-transform: capitalize;
  }
  .btn_group__item {
    border: 2px solid black;
    /* min-width: 6rem; */
    padding: 1.5rem 3rem;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
    margin: 2px;
    color: white;
    background-color:black;
    font-size: 17px;
    box-shadow: inset 0px 0px 0px -15px rgb(229, 229, 229);
    transition: all 300ms ease-out;
    text-transform: capitalize;
  }
  .btn_group__item:last-child {
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }
  .btn_group__item:first-child {
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
  }
  .btn_group__item:hover,
  .btn_group__item:focus {
    color: rebeccapurple;
    box-shadow: inset 0px -20px 0px -15px rgb(232, 228, 236);
  }
  .btn_group__item:focus {
    outline: none;
  }
  .btn_group__item:after {
    content: "✔️";
    margin-left: 0.5rem;
    display: inline-block;
    color: rgb(191, 184, 199);
    position: absolute;
    transform: translatey(10px);
    opacity: 0;
    transition: all 200ms ease-out;
  }
  .btn_group__item--active:after {
    opacity: 1;
    transform: translatey(-2px);
  }