
  .hello{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: sans-serif;
  }

  .grid_container {
    columns: 5 200px;
    column-gap: 1.5rem;
    width: 90%;
    margin: 0 auto;
    width: 150px;
    margin: 0 1.5rem 1.5rem 0;
    display: inline-block;
    width: 100%;
    border: solid 2px black;
    padding: 5px;
    box-shadow: 5px 5px 5px rgba(0,0,0,0.5);
    border-radius: 5px;
    transition: all .25s ease-in-out;
    
  }
 
  img{
    width: 100%;
   
    border-radius: 5px;
    transition: all .25s ease-in-out;
  }
  p {
    margin: 5px 0;
    padding: 0;
    text-align: center;
    font-style: italic;
  }
  
  
  
  