
.container {
  display: flex;
  border-radius: 1em;
  background-color: white;
  max-width: 1400px;
  margin: 120px;
  height: 300px;
  
  box-shadow: 0 0 20px -2px;
}


.container__half_image {
  flex: 1;
  
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 2em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
}

.container__half_content {
  padding: 2em;
  flex: 1;
}

.item_type {
  color: #767676;
  text-transform: uppercase;
  font: 1rem serif;
  margin-top: 0;
}

.item_title {
  color: black;
  font: bold 1.75rem/1.1 serif;
  margin: 0.625em 0;
}

.item_description {
  font: 1rem/1.4 serif;
}

.item_prices {
  display: flex;
  gap: 1em;
  margin: 1em 0;
}

.item_prices__current_price {
  color: green;
  font: bold 2rem/1.1 serif;
}

.item_prices__previous_price {
  color: #d7d7d7;
  font: bold 1rem/1.1 serif;
  place-self: center;
  text-decoration: line-through;
}

.btn {
  color: white;
  font: 1em/1.1 serif;
  font-weight: bold;
  padding: 1em 2em;
  width: 100%;
  border-radius: 1em;
  border: none;
}

.btn:hover {
  filter: brightness(1.5);
  transition-duration: 0.25s;
  transition-property: filter;
}

.btn:active {
  transform: translateY(3px);
}

.btn_positive {
  background-color: darkgreen;
}

@media screen and (max-width: 600px) {

 .hell{
    display: flex;
 }
  .container {
    flex-flow: column nowrap;
  }

  .container__half_image {
    border-top-right-radius: 1em;
    border-bottom-left-radius: 0;
    min-height: 200px;
  }
}
